/* fonts */
/* base */
*, *:before, *:after {
  box-sizing: border-box; }

html {
  font-size: inherit;
  line-height: 1.3;
  font-size: 16px; }

html, body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px; }

body {
  background: #ffffff;
  font-size: 16px;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: black;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%; }

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: black;
  font-weight: bold;
  text-decoration: none;
  margin: 0 0 15px 0; }

.h1, h1 {
  font-size: 21px;
  line-height: 1.05;
  letter-spacing: -1px;
  margin-bottom: 25px; }

.h2, h2 {
  font-size: 18px;
  line-height: 1.14;
  margin-bottom: 20px; }

.h3, h3 {
  font-size: 16px;
  line-height: 1.38; }

.h4, h4 {
  font-size: 14px;
  line-height: 1.38; }

.h5, h5 {
  font-size: 13px; }

.h6, h6 {
  font-size: 12px; }

small, .small {
  font-size: 12px;
  line-height: 1.2; }

p {
  line-height: 22px;
  margin: 0 0 20px 0; }

em, i, .em, .i {
  font-style: italic; }

strong, b, .strong, .b {
  font-weight: bold; }

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0; }

a, .link {
  color: blue;
  text-decoration: underline;
  outline: none;
  transition: color 0.35s, background 0.35s, border 0.35s; }
  a:hover, a:focus, a:active, .link:hover, .link:focus, .link:active {
    color: blue;
    text-decoration: none;
    outline: none; }

a.link:hover, a.link:focus, a.link:active {
  text-decoration: none; }

img, .img {
  box-sizing: content-box;
  display: inline-block;
  vertical-align: middle; }

.container {
  margin: 0 auto;
  width: 100%;
  min-width: 320px;
  padding: 0 15px; }

/* text align */
.text-center, .text--center {
  text-align: center !important; }

.text-left, .text--left {
  text-align: left !important; }

.text-right, .text--right {
  text-align: right !important; }

/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center; }

@keyframes bouncing-loader {
  to {
    opacity: 0.1;
    transform: translate3d(0, -1rem, 0); } }

.bouncing-loader {
  display: flex;
  justify-content: center; }
  .bouncing-loader > div {
    width: 1rem;
    height: 1rem;
    margin: 3rem 0.2rem;
    background: #8385aa;
    border-radius: 50%;
    animation: bouncing-loader 0.6s infinite alternate; }
    .bouncing-loader > div:nth-child(2) {
      animation-delay: 0.2s; }
    .bouncing-loader > div:nth-child(3) {
      animation-delay: 0.4s; }

/* img */
.img-responsive, .img--responsive {
  display: block;
  height: auto;
  max-width: 100%; }

.img-round, .img--round {
  border-radius: 50%; }

/* section */
.section {
  padding: 50px 0;
  min-width: 320px; }

.container .section {
  min-width: inherit; }

ul li {
  list-style-type: disc; }

ol li {
  list-style-type: decimal; }

/* bg */
.bg--pink {
  background-color: pink !important; }

.bg--white {
  background-color: #ffffff !important; }

.bg--black {
  background-color: #000000 !important; }

/* no padding no margin */
.no--p {
  padding: 0 !important; }
  .no--p-top {
    padding-top: 0 !important; }
  .no--p-bottom {
    padding-bottom: 0 !important; }
  .no--p-left {
    padding-left: 0 !important; }
  .no--p-right {
    padding-right: 0 !important; }

.no--m {
  margin: 0 !important; }
  .no--m-top {
    margin-top: 0 !important; }
  .no--m-bottom {
    margin-bottom: 0 !important; }
  .no--m-left {
    margin-left: 0 !important; }
  .no--m-right {
    margin-right: 0 !important; }

.no--b {
  background: none !important; }
  .no--b-image {
    background-image: none !important; }

.no--border {
  border: 0 !important; }

/* icons */
.icon {
  display: inline-block; }

/* color */
.color--black {
  color: #000000 !important; }

.color--white {
  color: #ffffff !important; }

/* buttons */
.btn {
  display: inline-block;
  padding: 0 15px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
  background-image: none;
  background-color: #cccccc;
  border: 1px solid #ffffff;
  border-radius: 3px;
  height: 40px;
  line-height: 40px;
  text-decoration: none;
  transition: all 0.35s; }
  .btn:hover, .btn:focus, .btn:active {
    outline: none; }
  .btn--default {
    color: #333;
    background-color: #ffffff;
    border-color: #cccccc; }

/* forms */
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  color: #cccccc; }

input::-moz-placeholder, textarea::-moz-placeholder {
  color: #cccccc; }

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: #cccccc; }

input:-moz-placeholder, textarea:-moz-placeholder {
  color: #cccccc; }

button, input, optgroup, select, textarea {
  margin: 0;
  font: inherit;
  color: inherit;
  outline: none; }
  button:hover, button:focus, button:active, input:hover, input:focus, input:active, optgroup:hover, optgroup:focus, optgroup:active, select:hover, select:focus, select:active, textarea:hover, textarea:focus, textarea:active {
    outline: none; }

button, input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
  outline: none; }

label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 700; }

.form-group, .form__group {
  margin-bottom: 15px; }

.form-control, .form__control {
  display: block;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #cccccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  transition: all .35s; }

.form-label, .form__label {
  display: block; }

textarea.form-control, textarea.form__control {
  height: 150px;
  resize: none; }

.has-success .form-control {
  border-color: #3c763d;
  box-shadow: inset 0 1px 1px rgba(60, 118, 61, 0.75); }

.has-success .control-label {
  color: #3c763d; }

.has-warning .form-control {
  border-color: #8a6d3b;
  box-shadow: inset 0 1px 1px rgba(138, 109, 59, 0.75); }

.has-warning .control-label {
  color: #8a6d3b; }

.has-error .form-control {
  border-color: #a94442;
  box-shadow: inset 0 1px 1px rgba(169, 68, 66, 0.75); }

.has-error .control-label {
  color: #a94442; }

/* menu */
[role="navigation"] ul {
  margin: 0;
  padding: 0; }
  [role="navigation"] ul li {
    list-style: none;
    margin: 0; }

/* header */
.header {
  background-color: #cccccc; }

/* footer */
.footer {
  background-color: #cccccc; }

